import ModalStyled from "./ModalStyled"

type Props = {
    display?: string
    img?: string
    title?:string
}

type State = {}

const Modal = ({ display, img, title }: Props) => {

    return (
        <ModalStyled display={display} >
            <div className="c">
                {title && <div className="t"> <p>{title}</p></div>}
                <div className="button-x buttons-x hide_modal">
                    <div className="deg45-r hide_modal"></div>
                    <div className="deg135-r hide_modal"></div>
                </div>
                {img && <div className="c2"> <img src={img} /></div> }
            </div>
        </ModalStyled>

    )
}

export default Modal