
import axios from "axios"
import React, { useState } from "react"
import { useNavigate } from "react-router-dom"
import Button from "../components/Button/Button"
import Input from "../components/Input/Input"
import Modal from "../components/Modal/Modal"
import Logo from "../images/logo.png"
import spinner from '../images/spinner.gif'
import { ResponseSuccess } from "../types/types"
import LoginStyled from "./LoginStyled"

type Props = {}

const Login = ({ }: Props) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [data, setData] = useState(Object);
  const [needShowModal, setNeedShowModal] = useState(false)
  // const [usernameError, setUsernameError] = useState('');
  //const [passwordError, setPasswordError] = useState('');

  interface ResponseData extends ResponseSuccess {
    data: Record<string, any>;
  }
  const navigate = useNavigate();

  const handleUsernameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setUsername(event.target.value);
  };

  const handlePasswordChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(event.target.value);
  };
  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    console.log("tentative login");

    try {
      setNeedShowModal(true)
      const response :ResponseData = await axios.post(`/Login.vtt?action=login&TxLogin=${username}&TxSenha=${password}`);
      var Data: Record<string, any>;

      if (typeof response.data === 'string') {
        Data = eval("(" + response.data + ")");
      } else {
        Data = response.data;
      }
      setData(Data);

      if (Data.success) {
        navigate("/home.vtt");
      } else {
        console.log(Data.error)
      }
      setNeedShowModal(false)
    } catch (error: unknown) {
      if (error instanceof Error) {
        console.log(error.message);
      }
    }
  }

  return (
    <LoginStyled >
      <form className="c" onSubmit={handleSubmit}>
        <div className="c_logo">
          <img src={Logo} />
        </div>
        <div className="c_input">
          <div className="c_field_name">
            LOGIN:
          </div>
          <Input onChange={handleUsernameChange} type="text" required={true}  onInvalid={e => e.target.setCustomValidity("Campo Usuário não pode ser vazio.")}
            onInput={e => e.target.setCustomValidity('')} />

        </div>
        <div className="c_input">
          <div className="c_field_name">
            SENHA:
          </div>

          <Input onChange={handlePasswordChange} type="password" required={true}  onInvalid={e => e.target.setCustomValidity("Campo Senha não pode ser vazio.")}
            onInput={e => e.target.setCustomValidity('')} />
          <div className="c_esq_senha">Esqueci minha senha</div>
        </div>
        <Button text="ENTRAR" />
      </form>
      <Modal display={needShowModal ? "flex": "none"} img={spinner} title="Validando Usuário" />
    </LoginStyled>

  )
}

export default Login;

