import styled from 'styled-components';

interface ModalProps {
  display?: string;
}

const ModalStyled = styled.div<ModalProps>` 
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.50);
    position: fixed;
    top: 0;
    display: ${({ display }) => display};

    >.c {
    position: relative;
    width: 400px;
    border-radius: 12px;
    padding-top: 42px;
    background-color: white;
    height: 194px;
    margin: auto;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1.08vw 1.08vw, rgba(0, 0, 0, 0.25) 0px 1.08vw 1.08vw inset;
     .button-x {
  /* border-radius: 0.97vw; */
  top: 0;
  right: 0;
}
>.button-x .deg45-r {
  transform: rotate(-45deg);
}
 >.button-x .deg135-r {
  transform: rotate(-135deg);
}
 >.button-x div {
  position: absolute;
    width: 4px;
    height: 32px;
    background: rgb(255, 246, 243);
    top: 5px;
    right: 18px;
}
 >.buttons-x {
  width: 42px;
    height: 42px;
    background: rgb(249, 24, 45);
    box-shadow: rgba(0, 0, 0, 0.25) -6px -6px 20px inset;
    border-radius: 10px;
    position: absolute;

}

>.t {
  top: 0px;
    position: absolute;
    width: 100%;
    left: 0px;
    height: 42px;
    font-size: 23px;
    border-radius: 10px 10px 10px 0px;
    color: rgb(255, 255, 255);
    background: rgb(33, 33, 33);
    >p {
     margin-left: 28px;
     margin-top: 6px;
    }
}
>.c2{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  >img {
    max-width: 100%;
    max-height: 100%;
  }
}

}

    
`

export default ModalStyled;